const jobs = (state = [], action) => {
	switch (action.type) {
		case "JOB_ADD":
			return [
				...state,
				{
					id: action.id,
					text: action.text,
					completed: false
				}
			];
		case "JOB_STATUS":
			return state.map(job => (job._id === action._id ? { ...job, status: action._status } : job));
		default:
			return state;
	}
};

export default jobs;



